import Vue from 'vue'
import Router from 'vue-router'
import Hitokoto from '@/components/Hitokoto'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hitokoto',
      component: Hitokoto
    }
  ]
})



